/* Kanban Board Main Container */
.kanban-board {
  width: 100%;
  max-width: 100%;
  min-height: 600px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  padding: 20px;
  box-sizing: border-box;
  border-radius: 12px;
  position: relative;
  overflow: hidden; /* Ensure content stays within container */
  display: flex;
  flex-direction: column;
}

/* Kanban Columns Container - WITH HORIZONTAL SCROLLING */
.kanban-columns {
  display: flex;
  gap: 16px;
  flex: 1;
  height: 100%;
  width: 100%;
  max-width: 100%;
  overflow-x: auto; /* Allow horizontal scrolling */
  overflow-y: hidden;
  padding-bottom: 15px; /* Extra space for horizontal scrollbar */
  box-sizing: border-box;
}

/* Individual Column - RESPONSIVE WIDTH WITH HORIZONTAL SCROLL */
.kanban-column {
  flex: 1; /* Equal width for all columns */
  min-width: 250px; /* Minimum column width for readability */
  max-width: 350px; /* Maximum column width to prevent too wide columns */
  display: flex;
  flex-direction: column;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  border: 1px solid #e0e0e0;
  transition: all 0.2s ease-in-out;
  box-sizing: border-box;
}

.kanban-column:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.15);
}

/* Column Header */
.kanban-column-header {
  padding: 16px 20px;
  color: white;
  font-weight: 600;
  font-size: 14px;
  text-align: center;
  border-radius: 12px 12px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  min-height: 50px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

/* Column Body */
.kanban-column-body {
  flex: 1;
  padding: 16px;
  overflow-y: auto;
  overflow-x: hidden;
  height: 0; /* Force flex to calculate height */
  display: flex;
  flex-direction: column;
  gap: 12px;
  min-height: 400px;
  box-sizing: border-box;
}

/* Kanban Cards - WITH SOURCE-BASED BACKGROUND */
.kanban-card {
  border-radius: 10px;
  border: 1.5px solid #e0e0e0;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
  padding: 18px 16px 12px 16px;
  margin-bottom: 12px;
  transition: all 0.2s ease-in-out;
  font-size: 15px;
  cursor: pointer;
}

/* Default card background */
.kanban-card {
  background: #fff;
}

/* Source-specific card backgrounds */
.kanban-card[data-source="VV"] {
  background: rgba(25, 118, 210, 0.08); /* Light blue for VV */
  border-color: rgba(25, 118, 210, 0.2);
}

.kanban-card[data-source="OF"] {
  background: rgba(255, 140, 0, 0.08); /* Light orange for OF */
  border-color: rgba(255, 140, 0, 0.2);
}

.kanban-card:hover {
  box-shadow: 0 8px 24px rgba(52, 152, 219, 0.15);
  border-color: #3498db;
  transform: translateY(-2px);
}

.kanban-card.active {
  opacity: 0.8;
  transform: rotate(2deg);
  z-index: 1000;
}

.kanban-card.overlay {
  transform: rotate(5deg);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
  z-index: 1001;
}

/* Card Header */
.kanban-card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 8px;
}

.kanban-card-title {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  color: #2c3e50;
  line-height: 1.3;
  flex: 1;
}

.kanban-card-priority {
  margin-left: 8px;
}

.priority-icon {
  font-size: 14px;
}

/* Card Content */
.kanban-card-content {
  margin-bottom: 12px;
}

.kanban-card-content p {
  margin: 0;
  font-size: 14px;
  color: #666;
  line-height: 1.4;
}

/* Card Footer */
.kanban-card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  font-size: 12px;
  color: #888;
}

.kanban-card-assignee,
.kanban-card-date {
  display: flex;
  align-items: center;
  gap: 4px;
}

.assignee-avatar,
.date-icon {
  font-size: 12px;
}

.assignee-name,
.date-text {
  font-size: 11px;
}

/* Card Tags */
.kanban-card-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  margin-top: 8px;
}

.kanban-card-tag {
  display: inline-block;
  background: #e3f2fd;
  color: #1976d2;
  border-radius: 10px;
  padding: 2px 8px;
  font-size: 12px;
  font-weight: 500;
  margin-right: 4px;
  margin-bottom: 2px;
}

.kanban-card-tag-more {
  background: #f5f5f5;
  color: #666;
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 10px;
  font-weight: 500;
}

