/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  animation: modalSlideIn 0.3s ease-out;
}

.modal-content {
  background: white;
  border-radius: 12px;
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-30px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.modal-header {
  padding: 24px 24px 16px;
  border-bottom: 1px solid #e0e0e0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h2 {
  margin: 0;
  color: #2c3e50;
  font-size: 24px;
  font-weight: 600;
}

.modal-close {
  background: none;
  border: none;
  font-size: 28px;
  color: #999;
  cursor: pointer;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.2s ease;
}

.modal-close:hover {
  background: #f0f0f0;
  color: #666;
}

.modal-body {
  padding: 20px 24px 24px;
}

.card-section {
  margin-bottom: 20px;
}

.card-section h3 {
  margin: 0 0 8px 0;
  color: #2c3e50;
  font-size: 16px;
  font-weight: 600;
}

.card-section p {
  margin: 0;
  color: #666;
  line-height: 1.6;
}

.priority-badge {
  color: white;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.tags-container {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-top: 8px;
}

.tag {
  background: #e3f2fd;
  color: #1976d2;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
}

.card-raw-data {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  padding: 12px;
  font-size: 12px;
  color: #495057;
  white-space: pre-wrap;
  overflow-x: auto;
  max-height: 200px;
  overflow-y: auto;
}

/* Droppable styling */
.kanban-column[data-droppable="true"] {
  background-color: rgba(52, 152, 219, 0.1);
}

.kanban-column[data-droppable="true"] .kanban-column-body {
  background-color: rgba(52, 152, 219, 0.05);
  border: 2px dashed #3498db;
  border-radius: 8px;
}

/* Responsive design with horizontal scrolling */
@media (max-width: 1200px) {
  .kanban-column {
    min-width: 240px;
    max-width: 320px;
  }
}

@media (max-width: 992px) {
  .kanban-column {
    min-width: 220px;
    max-width: 300px;
  }
}

@media (max-width: 768px) {
  .kanban-board {
    padding: 12px;
  }

  .kanban-columns {
    gap: 12px;
    overflow-x: auto;
    overflow-y: hidden;
    padding-bottom: 20px;
  }

  .kanban-column {
    min-width: 280px;
    max-width: 320px;
    flex: 0 0 280px; /* Fixed width for horizontal scrolling */
  }

  .kanban-column-body {
    max-height: 400px;
  }

  .modal-content {
    margin: 20px;
    width: calc(100% - 40px);
  }

  .modal-header {
    padding: 20px 20px 12px;
  }

  .modal-body {
    padding: 16px 20px 20px;
  }
}

@media (max-width: 480px) {
  .kanban-board {
    padding: 10px;
  }
  
  .kanban-columns {
    gap: 8px;
  }
  
  .kanban-column {
    min-width: 260px;
    max-width: 280px;
    flex: 0 0 260px; /* Fixed width for horizontal scrolling */
  }
  
  .kanban-column-body {
    padding: 12px;
  }
}

/* Scrollbar styling */
.kanban-column-body::-webkit-scrollbar,
.modal-content::-webkit-scrollbar,
.card-raw-data::-webkit-scrollbar {
  width: 8px;
}

.kanban-column-body::-webkit-scrollbar-track,
.modal-content::-webkit-scrollbar-track,
.card-raw-data::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.kanban-column-body::-webkit-scrollbar-thumb,
.modal-content::-webkit-scrollbar-thumb,
.card-raw-data::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

.kanban-column-body::-webkit-scrollbar-thumb:hover,
.modal-content::-webkit-scrollbar-thumb:hover,
.card-raw-data::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Horizontal scrollbar styling for columns container */
.kanban-columns::-webkit-scrollbar {
  height: 8px;
}

.kanban-columns::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.kanban-columns::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
  transition: background 0.2s ease;
}

.kanban-columns::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Additional field styling from the original */
.kanban-card-field-label {
  font-weight: 600;
  color: #666;
  margin-right: 4px;
}

.kanban-card-field-bold {
  font-weight: 600;
}

.kanban-card-badge {
  display: inline-block;
  background: #e74c3c;
  color: white;
  font-size: 11px;
  font-weight: 600;
  padding: 3px 8px;
  border-radius: 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 6px;
}

.kanban-card-badge-label {
  font-weight: 500;
  margin-right: 4px;
}

.kanban-card-badge-value {
  font-weight: 600;
}

.kanban-card-date-label {
  font-weight: 500;
  margin-right: 4px;
}

.kanban-card-date .date-text {
  font-weight: 500;
}

/* Drag and drop states */
.kanban-column.drag-over {
  background-color: rgba(52, 152, 219, 0.08);
  border: 2px dashed #3498db;
  transform: scale(1.02);
}

.kanban-card.dragging {
  opacity: 0.6;
  transform: rotate(3deg) scale(1.05);
  cursor: grabbing;
  z-index: 1000;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.kanban-card[draggable="true"] {
  cursor: grab;
  transition: all 0.2s ease-in-out;
}

.kanban-card[draggable="true"]:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.kanban-column {
  transition: all 0.2s ease-in-out;
}

.kanban-card {
  transition: all 0.2s ease-in-out;
}

/* Enhanced Permission-based styling */
.drop-disabled {
  background-color: rgba(220, 53, 69, 0.1) !important;
  border: 2px dashed #dc3545 !important;
}

.drop-disabled .kanban-column-header {
  background-color: #dc3545 !important;
  color: white !important;
}

.kanban-card.not-draggable {
  cursor: not-allowed !important;
  opacity: 0.7;
  position: relative;
}

/* 🎨 Enhanced Visual Feedback System - Orange/Green Logic */
.kanban-column.drag-target-allowed {
  background-color: rgba(76, 175, 80, 0.12) !important;
  border: 2px solid #4caf50 !important;
  transform: scale(1.02);
  box-shadow: 0 0 20px rgba(76, 175, 80, 0.3);
  transition: all 0.2s ease-in-out;
}

.kanban-column.drag-target-allowed .kanban-column-header {
  background-color: #4caf50 !important;
  color: white !important;
  box-shadow: 0 2px 10px rgba(76, 175, 80, 0.4);
}

.kanban-column.drag-target-allowed .kanban-column-header::after {
  content: "✅";
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
  font-weight: bold;
}

.kanban-column.drag-target-blocked {
  background-color: rgba(244, 67, 54, 0.12) !important;
  border: 2px solid #f44336 !important;
  opacity: 0.6;
  transform: scale(0.98);
  box-shadow: 0 0 20px rgba(244, 67, 54, 0.3);
  transition: all 0.2s ease-in-out;
  cursor: not-allowed;
}

.kanban-column.drag-target-blocked .kanban-column-header {
  background-color: #f44336 !important;
  color: white !important;
  box-shadow: 0 2px 10px rgba(244, 67, 54, 0.4);
}

.kanban-column.drag-target-blocked .kanban-column-header::after {
  content: "❌";
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
  font-weight: bold;
}

/* 🟡 Orange State: Available but conditional (your brilliant idea!) */
.kanban-column.drag-target-warning {
  background-color: rgba(255, 152, 0, 0.12) !important;
  border: 2px solid #ff9800 !important;
  transform: scale(1.01);
  box-shadow: 0 0 20px rgba(255, 152, 0, 0.3);
  transition: all 0.2s ease-in-out;
  position: relative;
}

.kanban-column.drag-target-warning .kanban-column-header {
  background-color: #ff9800 !important;
  color: white !important;
  box-shadow: 0 2px 10px rgba(255, 152, 0, 0.4);
}

.kanban-column.drag-target-warning .kanban-column-header::after {
  content: "🟡";
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
  font-weight: bold;
}

/* Special indicator for orange zones */
.kanban-column.drag-target-warning::before {
  content: "⚠️ Consider implications";
  position: absolute;
  top: -8px;
  left: 50%;
  transform: translateX(-50%);
  background: #ff9800;
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 10px;
  font-weight: 600;
  white-space: nowrap;
  z-index: 10;
  opacity: 0;
  animation: fadeInWarning 0.3s ease-in-out forwards;
}

@keyframes fadeInWarning {
  to {
    opacity: 1;
  }
}

/* Current stage (gray) - where the card is being dragged FROM */
.kanban-column.drag-target-current {
  background-color: rgba(158, 158, 158, 0.08) !important;
  border: 2px solid #9e9e9e !important;
  opacity: 0.7;
  transform: scale(0.99);
  box-shadow: 0 0 15px rgba(158, 158, 158, 0.2);
  transition: all 0.2s ease-in-out;
}

/* Current stage header */
.kanban-column.drag-target-current .kanban-column-header {
  background-color: #9e9e9e !important;
  color: white !important;
  box-shadow: 0 2px 10px rgba(158, 158, 158, 0.3);
}

.kanban-column.drag-target-current .kanban-column-header::after {
  content: "⚪";
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
  animation: fadeIn 0.3s ease-in-out;
}

.kanban-column.drag-target-current::before {
  content: "Current stage";
  position: absolute;
  top: -8px;
  left: 50%;
  transform: translateX(-50%);
  background: #9e9e9e;
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 10px;
  font-weight: 600;
  white-space: nowrap;
  z-index: 10;
  opacity: 0;
  animation: fadeInCurrent 0.3s ease-in-out forwards;
}

@keyframes fadeInCurrent {
  to {
    opacity: 1;
  }
}

/* Enhanced drag animations */
.kanban-column.drag-active {
  position: relative;
}

.kanban-column.drag-active::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 40%, rgba(255, 255, 255, 0.1) 50%, transparent 60%);
  animation: shimmer 2s infinite;
  pointer-events: none;
  z-index: 1;
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

/* Role-based visual indicators */
.kanban-column-header {
  position: relative;
}

.role-indicator {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  font-size: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  z-index: 10;
}

.role-indicator.visitor {
  background-color: #6c757d;
  color: white;
}

.role-indicator.sales {
  background-color: #17a2b8;
  color: white;
}

.role-indicator.portfolio-manager {
  background-color: #007bff;
  color: white;
}

.role-indicator.risk {
  background-color: #fd7e14;
  color: white;
}

.role-indicator.risk-manager {
  background-color: #e83e8c;
  color: white;
}

.role-indicator.admin {
  background-color: #28a745;
  color: white;
}

/* Deal card enhancements */
.kanban-card.deal-high-priority {
  border-left: 4px solid #dc3545;
}

.kanban-card.deal-medium-priority {
  border-left: 4px solid #ffc107;
}

.kanban-card.deal-low-priority {
  border-left: 4px solid #28a745;
}

.kanban-card.deal-vv-source {
  background: linear-gradient(135deg, rgba(25, 118, 210, 0.05) 0%, rgba(25, 118, 210, 0.15) 100%);
  border: 1px solid rgba(25, 118, 210, 0.2);
}

.kanban-card.deal-of-source {
  background: linear-gradient(135deg, rgba(255, 140, 0, 0.05) 0%, rgba(255, 140, 0, 0.15) 100%);
  border: 1px solid rgba(255, 140, 0, 0.2);
}

.kanban-card.deal-clickable {
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.kanban-card.deal-clickable:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

/* Tooltip system for drag feedback */
.drag-tooltip {
  position: absolute;
  background-color: #343a40;
  color: white;
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 12px;
  white-space: nowrap;
  z-index: 1000;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.drag-tooltip.show {
  opacity: 1;
}

.drag-tooltip.success {
  background-color: #28a745;
}

.drag-tooltip.warning {
  background-color: #ffc107;
  color: #212529;
}

.drag-tooltip.error {
  background-color: #dc3545;
}

.kanban-card.not-draggable::after {
  content: "🔒";
  position: absolute;
  top: 8px;
  right: 8px;
  font-size: 12px;
  opacity: 0.6;
}

.kanban-card.not-draggable:hover {
  transform: none !important;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1) !important;
}

.lock-icon {
  font-size: 14px;
  margin-left: 8px;
  opacity: 0.9;
}

/* ========================================
   READY TO MOVE FEATURE STYLES
   ======================================== */

/* Pulsating animation for cards ready to move */
.kanban-card.ready-to-move {
  border: 3px solid #10b981 !important;
  animation: pulse-ready 2.5s infinite;
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.2);
}

@keyframes pulse-ready {
  0% { 
    border-color: #10b981; 
    box-shadow: 0 4px 12px rgba(16, 185, 129, 0.2), 0 0 0 0 rgba(16, 185, 129, 0.7); 
  }
  50% { 
    border-color: #059669; 
    box-shadow: 0 4px 16px rgba(16, 185, 129, 0.3), 0 0 0 8px rgba(16, 185, 129, 0.3); 
  }
  100% { 
    border-color: #10b981; 
    box-shadow: 0 4px 12px rgba(16, 185, 129, 0.2), 0 0 0 0 rgba(16, 185, 129, 0); 
  }
}

/* Ready toggle button styles */
.ready-toggle-btn {
  transition: all 0.2s ease !important;
  border-radius: 50% !important;
  opacity: 0.7;
}

.ready-toggle-btn:hover {
  transform: scale(1.2) !important;
  opacity: 1;
}

.ready-toggle-btn.ready {
  filter: drop-shadow(0 0 4px rgba(16, 185, 129, 0.6));
  opacity: 1;
}

.ready-toggle-btn.not-ready {
  opacity: 0.6;
}

.ready-toggle-btn.not-ready:hover {
  opacity: 0.8;
}

/* Ensure ready button doesn't interfere with other card interactions */
.kanban-card.ready-to-move .ready-toggle-btn {
  background: rgba(255, 255, 255, 0.9) !important;
  border: 1px solid rgba(16, 185, 129, 0.3) !important;
}

/* Subtle visual enhancement for ready cards */
.kanban-card.ready-to-move .kanban-card-title {
  color: #059669;
  font-weight: 600;
}

/* Prevent animation conflicts during drag */
.kanban-card.ready-to-move.dragging {
  animation: none !important;
  border: 2px solid #10b981 !important;
} 